body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --color-blue: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.link {
  text-align: left;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.link:hover {
  color: var(--color-blue);
}

.nav__list li:first-child .link {
  margin-left: 0;
}

.link.is-active {
  color: var(--color-blue);
}

.link.is-active:after {
  content: "";
  background: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
